.App {
  text-align: center;
}
.wrapper { 
  width: 50%;
  height: auto;
  margin: 0 auto;
  margin-top: 30px;
  padding: 15px;
  background-color: #ffffff;
  border-radius: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.7);
}
.flex{
  display: flex;
  justify-content: space-between;
}
.margin-bottom{
  margin-bottom: 30px;
}
.btn-style{
  border: 0;
  border-radius: 10px;
  padding: 5px 10px;
  color: #ffffff;
  background-color: #1a5cd5;
  box-shadow: 0 0 5px rgba(0, 0, 0, .7);
  font-size: 20px;
}
.btn-position{

  display: flex;
  justify-content: start;
}

@media only screen and (max-width: 1200px){
  .wrapper{
    width: 60%;
  }
  
}
@media only screen and (max-width: 992px){
  .wrapper{
    width: 70%;
  }
}
@media only screen and (max-width: 768px){
  .wrapper{
    width: 80%;
  }
}
