.wrapperCard{
    /* width: 100%; */
    height: 50px;
    margin: 15px auto;
    border: dashed 1px;
    border-radius: 10px;
    padding:10px;
    background-color: #97d8b2f7;
    box-shadow: 0 0 5px rgba(16, 147, 71, 0.51);
}
.flex{
    height: 50px;
    display: flex;
    justify-content: space-between;
    align-items: center;
}
.h6{
    font-size: 25px;
    font-weight: normal;
    font-style: italic;
}

@media only screen and (max-width: 1200px){
    .h6{
        font-size: 20px;
    }
}
