.flex-input {
  display: flex;
}
.flex-input-btn{
    display: flex;
}
.input-style {
  border: 1px solid;
  border-radius: 5px;
  width: 50px;
  margin: 0 10px;
}
input {
  font-size: 18px;
  height: 30px;
}
.btn-style {
  border: 0;
  border-radius: 10px;
  padding: 5px 10px;
  color: #ffffff;
  background-color: #1a5cd5;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.7);
  font-size: 20px;
}
p {
  font-size: 20px;
}

@media only screen and (max-width: 1200px) {
    
}
@media only screen and (max-width: 992px) {
    .flex-input-btn{
        display: block;
    }
    .btn-style {
        margin: 10px;
    }
}
@media only screen and (max-width: 768px) {
}
@media only screen and (max-width: 480px) {
    .input-style{
        height: 25px;
        width: 40px;
        font-size: 15px;
    }
    p{
        margin-top: 3px;
        font-size: 15px;
    }
    .btn-style{
        font-size: 17px;
    }
    .input-style {
        margin: 0 5px;
    }
}
@media only screen and (max-width: 320px) {
}
