.wrapper-form{
    width: 50%;
    height: auto; 
    margin: 0 auto;
    margin-top: 30px;
    padding: 15px;
    background-color: #ffffff;
    border-radius: 10px;
    box-shadow: 0 0 5px rgba(0,0,0,.7);
}
label{
    font-size: 20px;
}
.labelControl{
    color:red;
}
.inputControl{
    border: 2px solid #000;
    background-color:salmon;
}
input{
    margin-top: 5px;
    padding: 3px 5px;
    /* border: 1px solid;
    border-radius: 5px; */
}
.container-label{
    font-size: 18px;
    letter-spacing: 1px;
}
.btn{
    margin: 15px;
    padding: 5px 10px;
    border: 0;
    border-radius: 10px;

    box-shadow: 0 0 5px rgba(0,0,0,.7); 
}
.btn-style{
    border: 0;
    border-radius: 10px;
    padding: 5px 10px;
    color: #ffffff;
    background-color: #1a5cd5;
    box-shadow: 0 0 5px rgba(0, 0, 0, .7);
    font-size: 20px;
}

@media only screen and (max-width: 1200px){
    .wrapper-form{
        width: 60%;
    }
    .btn{
        margin: 10px 7px;
    }
}
@media only screen and (max-width: 992px){
    .wrapper-form{
        width: 70%;
    }
}
@media only screen and (max-width: 768px){
    .wrapper-form{
        width: 80%;
    }
}
@media only screen and (max-width: 480px){
    .btn{
        margin: 10px 5px;
    }
}
